.hero {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	justify-content: center;
	min-height: 100vh;
	padding: 185px 0 100px 0;

	&__content {
		text-align: center; }

	&__title {
		display: block;
		margin-bottom: 8px;
		@include bebas-font;
		font-size: 200px;
		line-height: 1;
		font-weight: 100;
		letter-spacing: 5px;
		text-transform: uppercase;
		text-decoration: none;

		@include md {
			font-size: 100px; }

		@include sm {
			font-size: 70px; }

		@include xs {
			font-size: 30px; } }

	&__sub-title {
		margin-bottom: 94px; }

	&__btn {
		margin-bottom: 20px; } }

.logo {
	position: relative;
	top: 5px;
	display: block;
	max-width: 151px;
	max-height: 33px;

	&__img {
		max-width: 100%;
		height: auto;
		object-fit: contain; } }

.main-title {
	position: relative;
	display: block;
	padding-top: 40px;
	@include bebas-font;
	font-size: 24px;
	line-height: 1.25;
	text-decoration: none;
	text-transform: uppercase;
	letter-spacing: 1px;
	text-align: center;
	color: $white;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		@include center(h);
		@include arr(10,10, $white, t); }

	&--alignment {
		text-align: left;

		&::before {
			left: 0;
			transform: none; } } }

.about-sect {
	&__title {
		margin-bottom: 45px; }

	&__list {
		margin-bottom: 20px; }

	&__btn {
		margin-bottom: 20px;
		text-align: center; } }

.about {
	display: flex;

	@include sm {
		flex-wrap: wrap; }

	&__item {
		width: 33.33%;
		padding-right: 15px;
		@include clear-font;
		font-size: 14px;
		line-height: 20px;

		@include sm {
			width: 100%; } } }

.btn-default {
	display: inline-block;
	height: 60px;
	padding: 0 26px;
	background-color: transparent;
	font-size: 18px;
	line-height: 3.6;
	font-weight: 400;
	color: $color-important;
	text-decoration: none;
	user-select: none;
	touch-action: manipulation;
	white-space: nowrap;
	z-index: 3;

	&:hover,
	&:focus {
		opacity: 0.8; }

	@include xs {
		padding: 7px 22px;
		font-size: 18px; } }


.advantages-sect {
	&__list {
		display: flex;
		margin-bottom: 50px;

		@include md {
			flex-wrap: wrap;
			justify-content: center;
			margin-bottom: 30px; } }

	&__item {
		width: 20%;

		@include md {
			width: 33.33%; }

		@include sm {
			width: 100%; } } }


.advantages {
	padding: 15px;

	&__title {
		font-weight: 500; }

	&__media {
		& > svg {
			fill: $color-important; } } }

.details-sect {
	&__list {
		display: flex;
		margin-bottom: 50px;

		@include md {
			flex-wrap: wrap;
			justify-content: center;
			margin-bottom: 30px; } }

	&__item {
		width: 20%;

		@include md {
			width: 33.33%; }

		@include sm {
			width: 100%; } } }

.details-ico {
	padding-bottom: 28px;

	&__title {
		font-weight: 500;
		margin-bottom: 18px;
		padding-bottom: 15px;
		border-bottom: 1px solid rgba(#fdfefd, 0.20); } }

.person {
	&__img-wrap {
		position: relative;
		overflow: hidden;
		width: 160px;
		height: 160px;
		border-radius: 50%;
		opacity: 0.6;

		&::before {
			content: '';
			@include coverdiv;
			background-color: rgba($color-important, 0.73);
			transition: height .5s ease-in-out; }

		&:hover {
			&::before {
				height: 0; } } }

	&__img {
		display: block;
		max-width: 100%;
		height: 100%;
		object-fit: cover;
		border-radius: 50%; } }


.team-sect {
	padding: 50px 0;

	&__list {
		display: flex;
		flex-wrap: wrap; }

	&__item {
		width: 25%;

		@include sm {
			width: 50%; }

		@include xs {
			width: 100%; } } }

.partners-sect {
	padding: 50px 0;

	&__title {
		padding-right: 164px; }

	&__wrap {
		display: flex;
		align-items: center; }

	&__list {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-grow: 1; } }

.footer {
	padding: 50px 0;
	background-color: $color-important;

	&__title {
		padding-right: 200px; }

	&__content {
		display: flex;
		flex-direction: column;
		flex-grow: 1; }

	&__body {
		display: flex; }

	&__articles {
		display: flex;
		justify-content: space-between;
		flex-grow: 1; }

	&__pagination {
		display: flex; }

	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center; } }

.menu-info {
	display: flex;

	&__link {
		display: block;
		padding: 5px 15px;
		color: #fff;
		text-decoration: none;

		&:hover {
			opacity: 0.85; } } }

.pagination {
	$this: &;

	&__item {
		position: relative;
		color: #fff;

		&.is-active {
			color: #11052f;

			&::before {
				content: '';
				position: absolute;
				top: 0;
				@include center(h);
				width: 40px;
				height: 40px;
				border: 1px solid #11052f;
				border-radius: 50%; } } }

	&__link {
		display: block;
		padding: 15px;
		padding: 0 15px;
		line-height: 38px;
		color: currentColor;
		text-decoration: none;
		transition: color .25s ease-in-out;

		&:hover {
			color: #11052f; } } }


.articles-list {

	&__item {
		display: block;
		margin-bottom: 20px; } }

.article {
	display: block;
	color: $white;
	text-decoration: none;

	&:hover {
		opacity: 0.85; }

	&__info {
		display: block;
		color: #3d0425; } }


.social-list {
	display: flex;

	&__item {
		&:not(:last-of-type) {
			margin-right: 12px; } }

	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 34px;
		height: 34px;
		opacity: 0.4;

		svg {
			width: 34px;
			height: 34px;
			fill: $white; } } }
