.popup {
    overflow-y: scroll;
    z-index: 2;
    @include coverdiv;
    background-color: rgba(0,0,0,0.8);
    padding-bottom: 40px;
    $this: &;

    &.is-open {
        opacity: 1;
        visibility: visible;
        .room-brief {
            transform: scale(0.9);
            transform-origin: 0 100%;
            opacity: 1; }
        .room-description {
            &__info, &__controls {
                transform: translateY(0);
                opacity: 1; } } }

    &__gallery {
        width: percentage(972/1440);
        position: relative;
        @include sm {
            width: 100%;
            height: 370px; } }

    &__inner {
        width: 950px;
        position: absolute;
        top: 120px;
        left: 50%;
        transform: translateX(-50%);
        border-radius: 2px;
        background-color: #fff;

        @include md {
            width: 720px; }

        @include sm {
            top: 30px;
            width: 400px; }

        @include r(449) {
            width: 300px; } } }

