.burger {
	$this: &;
	position: relative;
	width: 100%;
	padding: 0;
	background: 0 0;
	border: 0;
	user-select: none;
	cursor: pointer;

	&.is-active {
		#{$this}__line {
			&:first-child {
				transform: rotate(45deg) translateY(6px) translateX(5px); }
			&:nth-child(2) {
				opacity: 0; }
			&:nth-child(3) {
				transform: rotate(316deg) translateY(-3px) translateX(1px); }

			&::before {
				width: 100%; } } }

	&__line {
		position: relative;
		display: block;
		width: 100%;
		width: 24px;
		height: 2px;
		margin-bottom: 3px;
		background: $white;
		will-change: transform;
		transition: .3s;

		&:nth-child(2) {
			width: 16px; }

		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 0;
			height: 2px;
			background: $color-important;
			transition: width .3s;
			box-sizing: border-box;
			transition: .75s; } } }
