html {
	overflow-x: hidden; }

body {
	padding: 20px;
	@include clear-font;
	font-size: 16px;
	line-height: 1.4;
	font-weight: 400;
	color: $white;
	background-color: $bg-color;
	background-image: url("../img/hero/bg-hero.jpg"), url("../img/hero/bg-horizont.png");
	background-repeat: no-repeat;
	background-size: contain;
	background-position: top center;
	// background-attachment: fixed
	box-shadow: inset 0px 0px 0 0px $color-important;
	transition: box-shadow 0.6s linear;
	@include smooth-font;
	overflow-x: hidden;

	&.is-overflow-hidden {
		height: 100vh;
		overflow: hidden;
		width: 100vw; }

	&.is-hidden {
		height: 100%;
		width: 100%;
		position: fixed;
		overflow: hidden; }

	&.is-load {
		box-shadow: inset 0px 0px 0 20px $color-important; } }

.is-mob-hidden {
	@include sm {
		display: none; } }

.is-mob-visible {
	display: none;

	@include sm {
		display: block; } }

.container {
	max-width: #{$desktop + ($padding--md * 2)}px;
	margin: 0 auto;
	padding: 0 #{$padding--md}px; }

@include selection {
	background-color: $color-important;
	color: $white; }

