.btn {
	display: inline-block;
	height: 60px;
	padding: 0 26px;
	background-color: $color-important;
	@include bebas-font;
	line-height: 1.9;
	font-weight: 400;
	font-size: 22px;
	color: $white;
	text-decoration: none;
	border-radius: 7px;
	user-select: none;
	touch-action: manipulation;
	white-space: nowrap;
	z-index: 3;

	&:hover,
	&:focus {
		opacity: 0.8; }

	@include xs {
		padding: 7px 22px;
		font-size: 18px; }

	&--dark {
		color: $white;
		background-color: $bg-color; } }
