html {
  overflow-x: hidden;
}

body {
  padding: 20px;
  font-family: "clear", sans-serif;
  font-size: 16px;
  line-height: 1.4;
  font-weight: 400;
  color: #ffffff;
  background-color: #11052f;
  background-image: url("../img/hero/bg-hero.jpg"), url("../img/hero/bg-horizont.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top center;
  box-shadow: inset 0px 0px 0 0px #932063;
  transition: box-shadow 0.6s linear;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

body.is-overflow-hidden {
  height: 100vh;
  overflow: hidden;
  width: 100vw;
}

body.is-hidden {
  height: 100%;
  width: 100%;
  position: fixed;
  overflow: hidden;
}

body.is-load {
  box-shadow: inset 0px 0px 0 20px #932063;
}

@media only screen and (max-width: 767px) {
  .is-mob-hidden {
    display: none;
  }
}

.is-mob-visible {
  display: none;
}

@media only screen and (max-width: 767px) {
  .is-mob-visible {
    display: block;
  }
}

.container {
  max-width: 1239px;
  margin: 0 auto;
  padding: 0 20px;
}

::selection {
  background-color: #932063;
  color: #ffffff;
}

.hero {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  min-height: 100vh;
  padding: 185px 0 100px 0;
}

.hero__content {
  text-align: center;
}

.hero__title {
  display: block;
  margin-bottom: 8px;
  font-family: "bebas", sans-serif;
  font-size: 200px;
  line-height: 1;
  font-weight: 100;
  letter-spacing: 5px;
  text-transform: uppercase;
  text-decoration: none;
}

@media only screen and (max-width: 1023px) {
  .hero__title {
    font-size: 100px;
  }
}

@media only screen and (max-width: 767px) {
  .hero__title {
    font-size: 70px;
  }
}

@media only screen and (max-width: 599px) {
  .hero__title {
    font-size: 30px;
  }
}

.hero__sub-title {
  margin-bottom: 94px;
}

.hero__btn {
  margin-bottom: 20px;
}

.logo {
  position: relative;
  top: 5px;
  display: block;
  max-width: 151px;
  max-height: 33px;
}

.logo__img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.main-title {
  position: relative;
  display: block;
  padding-top: 40px;
  font-family: "bebas", sans-serif;
  font-size: 24px;
  line-height: 1.25;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: center;
  color: #ffffff;
}

.main-title::before {
  content: '';
  position: absolute;
  top: 0;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 5px 10px 5px;
  border-color: transparent transparent #ffffff transparent;
}

.main-title--alignment {
  text-align: left;
}

.main-title--alignment::before {
  left: 0;
  transform: none;
}

.about-sect__title {
  margin-bottom: 45px;
}

.about-sect__list {
  margin-bottom: 20px;
}

.about-sect__btn {
