  color: #932063;
}

@media only screen and (max-width: 767px) {
  .nav {
    position: relative;
    height: 20px;
    justify-content: space-between;
    margin-top: 40px;
  }
}

.nav__burger {
  top: 0;
  right: 0;
  z-index: 25;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  width: 30px;
  height: 30px;
  padding-left: 15px;
}

.nav__list {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .nav__list {
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 20;
    min-width: 320px;
    padding-top: 15px;
    padding-right: 16px;
    padding-left: 65px;
    overflow-y: auto;
    color: #fff;
    background-color: #11052f;
  }
}

.nav__item:last-child {
  padding-right: 0;
}

@media only screen and (max-width: 767px) {
  .nav__item:last-child {
    padding-right: 10px;
  }
}

.nav__link {
  position: relative;
  display: block;
  padding: 9px;
  color: currentColor;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
}

.nav__link:hover, .nav__link:focus {
  color: #ffffff;
}

.nav__link:hover::before, .nav__link:focus::before {
  top: 109%;
  opacity: .6;
}

.nav__link:active {
  opacity: 0.9;
}

.nav__link::before {
  content: "";
  position: absolute;
  top: 80%;
  left: 0;
  right: 0;
  border-bottom: 2px solid #ffffff;
  opacity: 0;
  transition: opacity .25s,top .25s;
}

@media only screen and (max-width: 767px) {
  .nav__link::before {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .nav__link {
    padding: 10px 15px;
  }
}

.burger {
  position: relative;
  width: 100%;
  padding: 0;
  background: 0 0;
  border: 0;
  user-select: none;
  cursor: pointer;
}

.burger.is-active .burger__line:first-child {
  transform: rotate(45deg) translateY(6px) translateX(5px);
}

.burger.is-active .burger__line:nth-child(2) {
  opacity: 0;
}

.burger.is-active .burger__line:nth-child(3) {
  transform: rotate(316deg) translateY(-3px) translateX(1px);
}

.burger.is-active .burger__line::before {
  width: 100%;
}

.burger__line {
  position: relative;
  display: block;
  width: 100%;
  width: 24px;
  height: 2px;
  margin-bottom: 3px;
  background: #ffffff;
  will-change: transform;
  transition: .3s;
}

.burger__line:nth-child(2) {
  width: 16px;
}

.burger__line::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 2px;
  background: #932063;
  transition: width .3s;
  box-sizing: border-box;
  transition: .75s;
}

