.popup {
  overflow-y: scroll;
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  padding-bottom: 40px;
}

.popup.is-open {
  opacity: 1;
  visibility: visible;
}

.popup.is-open .room-brief {
  transform: scale(0.9);
  transform-origin: 0 100%;
  opacity: 1;
}

.popup.is-open .room-description__info, .popup.is-open .room-description__controls {
  transform: translateY(0);
  opacity: 1;
}

.popup__gallery {
