.nav {
	$this: &;
	display: block;
	color: $color-important;

	&.is-open {

		#{$this}__list {
			// display: flex
			// height: 100%
 } }			// position: fixed

	@include sm {
		position: relative;
		height: 20px;
		justify-content: space-between;
		margin-top: 40px; }

	&__burger {
		top: 0;
		right: 0;
		z-index: 25;
		justify-content: center;
		align-items: center;
		box-sizing: content-box;
		width: 30px;
		height: 30px;
		padding-left: 15px; }

	&__list {
		display: flex;
		align-items: center;

		@include sm {
			display: none;
			// height: 0
			position: fixed;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 20;
			min-width: 320px;
			padding-top: 15px;
			padding-right: 16px;
			padding-left: 65px;
			overflow-y: auto;
			color: #fff;
			background-color: #11052f;
 } }			// transition: height .7s ease

	&__item {

		&:last-child {
				padding-right: 0;

				@include sm {
					padding-right: 10px; } } }

	&__link {
		position: relative;
		display: block;
		padding: 9px;
		color: currentColor;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
		text-decoration: none;

		&:hover,
		&:focus {
			color: $white;

			&::before {
				top: 109%;
				opacity: .6; } }

		&:active {
			opacity: 0.9; }

		&::before {
			content: "";
			position: absolute;
			top: 80%;
			left: 0;
			right: 0;
			border-bottom: 2px solid $white;
			opacity: 0;
			transition: opacity .25s,top .25s;

			@include sm {
				display: none; } }


		@include sm {
			padding: 10px 15px; } } }
