.language {
	$this: &;

	&__list {
		display: flex; }

	&__item {
		position: relative;

		&.is-active {
			#{$this}__link {
				color: #ffffff; } } }

	&__link {
		display: block;
		padding: 9px;
		color: $color-important;
		font-size: 12px;
		line-height: 1.25;
		font-weight: 700;
		text-transform: uppercase;
		text-decoration: none;

		&:hover {
			color: #ffffff;
			opacity: 0.8; } } }
